@media screen and (max-width: 947px) {
 .responsiveTable thead{
  display: none
}
.responsiveTable tbody{
  display: flex;
  flex-direction: column;
}
.responsiveTable tr{
  min-height: 200px;
  height: auto;
  padding-left:10px;
  flex-direction: column;
  display:flex
}
.responsiveTable th,td{
  border:none;
  padding-left: 0 !important;
  display:flex;
}
.responsiveTable tbody th[data-title]:before {
  content: attr(data-title);
  margin-left: 10px;
  margin-right: 10px;
  font-weight: 600;
}
.number_fileld_resource {
   font-size: 12px; 
}
} 
