/* tablescroll */

.table-wrapper {
  position: relative; 
}
.table-scroller {
  height:300px;
  overflow:auto;  
  margin-top:15px;
  margin-right:5px;
}

.table-wrapper table thead th .head { 
        z-index:2px;        
        width:'35%'
}

.table-scroller table {
  width:100%;
}

.scrollbar::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}

.scrollbar::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
}

.scrollbar::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color:#999999;
}


.scrollbar::-moz-scrollbar {
  width: 12px;
  }
.scrollbar::-moz-scrollbar-track {
  -moz-box-shadow: inset 0 0 6px #fff;
  background:#000;
  }
.scrollbar::-moz-scrollbar-thumb {
  border-radius: 10px;
  -moz-box-shadow: inset 0 0 6px #fff;
  background:#7A252B;
  }


.tabIndicator {
  background-color: #1692F2 !important
}
.tabOutline button{  
      border: 1px solid lightgray !important
}


